body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.hidden {
  display: none !important;
}

body {
  /* overflow: hidden; */
}


  .chip-label {
    font-size:18px;
    color: #c6305a;
    padding:10px 0 10px 0 ;
  }
 .chip-ul {
    padding: 0px 10px 0 10px;
    display: flex;
    flex-wrap: wrap;

  }
  
   .chip-ul .chip-li {
    list-style: none;
    margin-top: 10px;
    margin-left: 10px;
    border-radius: 5px;
    border: solid 1px #c6305a;
    padding : 2px 0px 2px 1px
  }
  .chip-ul .chip-li-selected {
    background-color: #ef5843;
  }
  .chip-li .inner-ul{
    padding: 2px 20px 0 20px;
  }
  .chip-li .inner-li{
    cursor: pointer;
    left:-10px
  }
  .chip-li .chip-name {
    position: 'relative';
    padding: 0;
     left: '-15px';
  }
  .Line-65 {
    width: 261px;
    height: 2px;
    object-fit: contain;
    background-image: -webkit-radial-gradient(0 0, circle, rgba(41, 41, 41, 0), #a2a2a2, rgba(255, 255, 255, 0), rgba(0, 0, 0, 0));
    background-image: radial-gradient(circle at 0 0, rgba(41, 41, 41, 0), #a2a2a2, rgba(255, 255, 255, 0), rgba(0, 0, 0, 0));
  }
.calendar {
  width: 400;
}


.side-menu {
  width: 20%;
  position: absolute;
  background-color: #eee;
  padding: 50px 20px 1px 15px;
}
.search {
  flex-direction: row;
  position: relative;
  display: flex;
  padding: 0 0 20px 0;
}

.searchTerm {
  width: 80%;
  border: 1px solid #f16552;
  padding: 5px;
  height: 40px;
  border-radius: 5px;
  outline: none;
  color: #9DBFAF;
}

.searchTerm:focus{
  color: #f16552;
}



/*Resize the wrap to see the search bar change!*/
.wrap{
 padding: 0 0 0 0;
}
.home {
  margin-left: 350px;
  height: 1000px;
  overflow: scroll;
}
p{
  margin:0;
}
.instructor-name{
  font-size: 20px;
  color: #545454;

}
.date-time{
  display: flex;
}
.date-time p {
  padding: 7px 15px;
  margin: 0px;
  font-weight: 800;
}
.date-time p:not(:first-child){
  border-left:1px solid #000;
}
.action-view p{
  font-weight: 800;
  font-size: 17px;
}
.action-view p span{
  font-weight: 100;
  padding-right: 10px;
  font-size: 13px;
}
.class-thumb {
  width: 122px;
  height: 126px;
}
.card-right {
  padding: 0 0 0 10px;
  display: flex;
}
.class-detail{
  display: flex;
  padding: 0 0 0 10px;
}

.shadow-cart {
  border: 1px solid #eee;
    padding: 15px;
    box-shadow: 0 1px 5px 0 rgba(0, 0, 0, 0.2);
    margin: 20px;
}
